import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Book } from './book.service';
import { Member } from './member.service';

export interface Loan {
  id?: number;
  book: Book;
  member: Member;
  loanDate: Date;
  dueDate: Date;
  returnDate?: Date;
  status: string;
}

@Injectable({
  providedIn: 'root'
})
export class LoanService {
  private baseUrl = '/api/loans';

  constructor(private http: HttpClient) {}

  getAllLoans(): Observable<Loan[]> {
    return this.http.get<Loan[]>(this.baseUrl);
  }

  getLoanById(id: number): Observable<Loan> {
    return this.http.get<Loan>(`${this.baseUrl}/${id}`);
  }

  createLoan(loan: any): Observable<Loan> {
    return this.http.post<Loan>(this.baseUrl, loan);
  }

  updateLoan(id: number, loan: any): Observable<Loan> {
    return this.http.put<Loan>(`${this.baseUrl}/${id}`, loan);
  }

  returnBook(id: number): Observable<Loan> {
    return this.http.put<Loan>(`${this.baseUrl}/${id}/return`, {});
  }

  deleteLoan(id: number): Observable<void> {
    return this.http.delete<void>(`${this.baseUrl}/${id}`);
  }
}
